body,
html {
  padding: 0;
  margin: 0;
}
ul {
  list-style: none;
  padding-left: 0px;
}

h1,
h2,
h3,
h4,
h5 {
  margin: 0px;
}

img {
  display: block;
  height: auto;
}

.link {
  text-decoration: none;
}
